.row {
    display: flex;
    flex-wrap: wrap;
    @media screen and (min-width: 767px) {
        gap: 1rem;
    }
}
.col {
    flex: 0 1 100%;
    margin: 0 auto 30px;
    &:last-child {
        margin-bottom: 0;
    }
    @media screen and (min-width: 767px) {
        flex: 0 1 calc((100% / 2) - 2rem);
    }
}

.input {
    &Wrap {
        position: relative;
        margin-bottom: 30px;
    }
}

.divider {
    margin: 30px 0;
    background-color: #f0f0f0;
    height: 1px;
}

[data-theme=dark] {
    .divider {
        background-color: #1c1d22;
    }
}